
/**
 * @function waitForElement
 *  @param  {String} selector The selector of the desired element
 *  @param  {Number} checkFrequencyInMs How often to check for element in Milliseconds
 *  @param  {Number} timeoutInMs How long to keep checking in milliseconds before timing out
 * @return {Promise} Resolves to the element if found
 * @example
 * //Here it is waiting on an element by class name for 10 seconds, and checking every half a second for the element
 * 
 * waitForElement('.vsc-initialized',500,10_000).then((e)=>{console.log('element found'); return e;})
 */

export function waitForElement(
  selector,
  checkFrequencyInMs = 500,
  timeoutInMs = 10000
) {
  const startTimeInMs = Date.now();
  return new Promise(function (resolve) {
    (function checkIfElementExists() {
      const element = document.querySelector(selector);
      if (element !== null) {
        resolve(element);
      } else {
        setTimeout(() => {
          //checking
          // console.log("checking");
          if (timeoutInMs && Date.now() - startTimeInMs > timeoutInMs) {
            //time out
            return;
          }
          checkIfElementExists();
        }, checkFrequencyInMs);
      }
    })();
  });
}
