/**
   * Easily Create HTML elements
   * 
   * Remember: 'className' for classes not just 'class'
   * 
   * [Original Author](https://javascript.plainenglish.io/how-to-build-complex-dom-elements-quickly-b3ead8e09647)
   * @function createElement
   * @param  {Object} object - The object to build the HTML element
   * @return {HTML Element}
   * @example 
   * document.body.appendChild(createElement({
            type:'section',
            className:'secion-1', 
            id:'section-1-id',
            innerHTML:'This is an Example',
            attributes:{
              dataFoo:'data Test 1',
              dataBaz:'data Test 2',
              onclick:'someFuncHere(this);'
            }
          }))
   */
  export function createElement(object) {
    object.type = object.type || 'div';
    let element = document.createElement( object.type);
    
      // iterate through properties
      for (const key in object) {
        if (key != 'attributes' && key != 'type') {
          element[key] = object[key];
        }
      }
      if (object.attributes) {
          for (let key in object.attributes) {
            
            //get value before adding '-' to key name
            let value = object.attributes[key];
              //at least one capital letter present
              if (key != key.toLowerCase()) {
                  // dataFoo gets changed to data-foo because F is replaced with -f.
                  key = key.replace(/[A-Z]/g, capitalLetter => `-${capitalLetter.toLowerCase()}`);
               }
               element.setAttribute(key, value);
       
           }
       }
      return element;
  }