/**
 * Dynamically add Fonts to a Page
 * 
 * [Additional Notation](https://github.com/typekit/fvd)
 * @function addFontFace
 * @param {Array} families An array list of google font families to add to a page.
 * @example
 * 
 * //Font Style
 * 
n: normal  (default)
i: italic
o: oblique


(no properties)
=> n4

font-style: italic;
=> i4

font-weight: normal;
=> n4

font-weight: bold;
=> n7

font-style: normal;
font-weight: 400;
=> n4

font-style: italic;
font-weight: 500;
=> i5

//usage:

 * addFontFace(['Praise:400','Montserrat:400,700,i4,i7','Roboto:i5'])
 */
export function addFontFace 
    ( 
        families = ['Fira Code:n4,n7','Inconsolata','Roboto:i5,n4','Praise:400','Montserrat:400,700,i4,i7','Monsieur La Doulaise:400','Dancing Script:400,i4','Ruda:n4']
    ) {
 
   (function(document) {
    let webFontScriptTag = document.createElement('script');
    let currentFirstScript = document.scripts[0];
    webFontScriptTag.src = 'https://ajax.googleapis.com/ajax/libs/webfont/1.6.26/webfont.js';
    webFontScriptTag.async = true;
    webFontScriptTag.classList.add('tools-clickwithclark-fonts');
    currentFirstScript.parentNode.insertBefore(webFontScriptTag, currentFirstScript); 
    webFontScriptTag.addEventListener('load',()=>{
      WebFont.load({
        google: {
          families
        }
      });
     })
   })(document);

   


   };