
/**
 * Copies an object and all it's nested properties
 * 
 * More Info: [MDN Web Docs](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/forEach#An_object_copy_function)
 * 
 * @function deepCopy
 * @param  {Object} object The object to copy
 * @return {Object} The copied object
 * 
 * 
 *
 */
export function deepCopy(object) {
    const copy = Object.create(Object.getPrototypeOf(object))
    const propNames = Object.getOwnPropertyNames(object)
    
    propNames.forEach(function(name) {
    const desc = Object.getOwnPropertyDescriptor(object, name)
    Object.defineProperty(copy, name, desc)
    })
    
    return copy
    }