
/**
 * Generate an alphanumeric unique ID
 * No duplicates found in a 10 Million ID generation test
 * Ideal for small prototypes/scenarios.
 * 
 * For serious commercial projects use [nanoid](https://npmjs.com/package/nanoid).
 * 
 * 
 * @function generateID
 * @return {String} A unique ID
 */
export function generateID() {
    return window.crypto.getRandomValues(new Uint32Array(10))[Math.floor((Math.random()*10-1) + 1)].toString(16) +  
    window.crypto.getRandomValues(new Uint32Array(10))[Math.floor((Math.random()*10-1) + 1)].toString(16) 
  }
  